<div><div class="button-group my-4"><button :class="getActiveButton(CurrencyType.EUR)">EUR</button><button :class="getActiveButton(CurrencyType.USD)">USD</button><Button :class="getActiveButton(CurrencyType.RUB)">RUB</Button></div><table><thead><tr><th>Coin</th><th>Value</th></tr></thead><tbody><tr v-for="coin, i in Object.values(coins)" :key="i"><td>{{ coin.name }}</td><td>{{ getPriceByCurrency(coin) }}</td></tr></tbody></table></div>